import { Form, Link, useLoaderData } from "@remix-run/react";
import connectDb from "~/db/connectDb.server";
import { getSession } from "~/sessions";

export async function loader({ params, request }) {
  const db = await connectDb();
  const data = await db.models.Post.findById(params.postId);

  const session = await getSession(request.headers.get("Cookie"));
  const userId = session.get("userId");
  if (userId) {
    // Return the data and the user. We need the user to check if the user is admin.
    const user = await db.models.User.findById(userId);
    return { data, user };
  } else {
    return { data };
  }
}

export default function PostCard({ post }) {
  const loaderData = useLoaderData();
  return (
    <div className="pt-30 mx-auto text-center bg-slate-300 w-2/5 rounded-lg mb-8 drop-shadow-lg">
      <div className="">
        <h1 className="text-center text-bold">{loaderData?.data?.title}</h1>
        <p className="text-center text-bold"></p>
        <p>{loaderData?.data?.description}</p>
      </div>
      {loaderData?.user?.isAdmin && (
        <div className="flex justify-center items-center">
          <Link to={`/${loaderData?.data?._id}/edit`}>
            <button className="buttonPrimary">Edit</button>
          </Link>
          <Form method="post" action={`/${loaderData?.data?._id}/delete`}>
            <button type="submit" className="buttonDanger">
              Delete
            </button>
          </Form>
        </div>
      )}
    </div>
  );
}
